.btn-secondary {
    background-color:white;
    border-color: white;
    color: black;
    
  
    &:hover {
      color: black;
      background-color:white;
      border-color: white;
    }
  }

  .arun{
    box-shadow: 0px 0px 3px -1px;
    display: inline-block;
    border-radius: 8px;
  }

  .btn-secondary:not(:disabled):not(.disabled):active,
.btn-secondary:not(:disabled):not(.disabled).active,
.show > .btn-secondary.dropdown-toggle {
  background-color: #00194c;
  border-color: #00194c;
  color: white;
  border-radius: 0px;
  box-shadow: 1px 1px 5px -1px;

}
 